.profile-header {
  width: 100%;
  padding: 2rem;
  padding-bottom: 1.5rem;
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
  gap: 2rem;
}

.profile-picture {
  width: 200px;
  height: 200px;
  border-radius: 8px;
  object-fit: cover;
  border: none;
  flex-shrink: 0;
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.6), 0 0 40px rgba(59, 130, 246, 0.4);
}

.profile-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 1;
}

.profile-name {
  font-size: 4rem;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: 0.05em;
  line-height: 1.1;
  margin: 0;
  margin-bottom: 0.75rem;
}

.profile-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
}

.profile-subtitle {
  font-size: 1.1rem;
  color: var(--text-secondary);
  font-weight: 400;
  margin: 0;
  text-align: left;
}

.profile-school {
  font-size: 1rem;
  color: var(--text-secondary);
  font-weight: 400;
  margin: 0;
  text-align: left;
}

@media (max-width: 768px) {
  .profile-header {
    padding: 1.5rem;
    flex-direction: column;
  }
  
  .profile-picture {
    width: 150px;
    height: 150px;
  }
  
  .profile-name {
    font-size: 3rem;
  }
  
  .profile-subtitle {
    font-size: 1rem;
  }
  
  .profile-school {
    font-size: 0.95rem;
  }
}
