* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-primary: #1a1a2e;
  --bg-secondary: #16213e;
  --bg-gradient-start: #0f0c29;
  --bg-gradient-end: #302b63;
  --text-primary: #ffffff;
  --text-secondary: #e0e0e0;
  --accent-blue: #3b82f6;
  --accent-blue-light: #60a5fa;
  --card-bg: rgba(59, 130, 246, 0.1);
  --card-border: rgba(59, 130, 246, 0.3);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000000;
  color: var(--text-primary);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  position: relative;
}

.App {
  min-height: 100vh;
  position: relative;
  width: 100%;
}

.background-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 0;
  overflow: hidden;
}

.background-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.05);
  z-index: 1;
  pointer-events: none;
}

.content-wrapper {
  position: relative;
  z-index: 2;
  min-height: 100vh;
}

.container {
  display: flex;
  gap: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  padding-top: 0;
}

.sidebar {
  width: 30%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.main-content {
  width: 70%;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

@media (max-width: 968px) {
  .container {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
  }
  
  .main-content {
    width: 100%;
  }
}
