.research-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.section-heading {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: 0.05em;
  margin-bottom: 0.5rem;
  text-align: center;
}

.research-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.research-card {
  background: rgba(59, 130, 246, 0.08);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  transition: all 0.3s ease;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.research-card:hover {
  background: rgba(59, 130, 246, 0.12);
  border-color: rgba(59, 130, 246, 0.3);
}

.research-header {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  margin-bottom: 0.75rem;
}

.research-icon {
  font-size: 1.25rem;
  color: var(--accent-blue-light);
  display: flex;
  align-items: center;
  margin-top: 0.15rem;
  flex-shrink: 0;
}

.research-title-section {
  flex: 1;
}

.research-company {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  margin-bottom: 0.2rem;
}

.research-role {
  font-size: 0.95rem;
  color: var(--text-primary);
  font-weight: 500;
  margin: 0;
}

.research-dates {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin: 0;
  white-space: nowrap;
}

.research-summary {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin: 0;
  margin-bottom: 0.75rem;
}

.research-bullets {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  flex: 1 0 auto;
}

.research-bullets li {
  font-size: 0.85rem;
  color: var(--text-secondary);
  line-height: 1.5;
  padding-left: 1.25rem;
  position: relative;
}

.research-bullets li::before {
  content: '•';
  position: absolute;
  left: 0;
  color: var(--accent-blue-light);
  font-weight: bold;
}

.placeholder-text {
  background: rgba(59, 130, 246, 0.08);
  border: 1px dashed rgba(59, 130, 246, 0.3);
  border-radius: 12px;
  padding: 2rem;
  text-align: center;
  color: var(--text-secondary);
  font-style: italic;
}
